// Grid settings
$main-sm-columns:       12;
$sidebar-sm-columns:    4;

$white:  #fff !default;
$gray-100: #f8f9fa !default;
$gray-200: #e9ecef !default;
$gray-300: #dee2e6 !default;
$gray-400: #ced4da !default;
$gray-500: #adb5bd !default;
$gray-600: #868e96 !default;
$gray-700: #495057 !default;
$gray-800: #343a40 !default;
$gray-900: #212529 !default;
$black:  #000 !default;

$grays: (
  100: $gray-100,
  200: $gray-200,
  300: $gray-300,
  400: $gray-400,
  500: $gray-500,
  600: $gray-600,
  700: $gray-700,
  800: $gray-800,
  900: $gray-900
) !default;

$blue:    #007bff !default;
$indigo:  #6610f2 !default;
$purple:  #6f42c1 !default;
$pink:    #e83e8c !default;
$red:     #dc3545 !default;
$orange:  #fd7e14 !default;
$yellow:  #ffc107 !default;
$green:   #28a745 !default;
$teal:    #20c997 !default;
$cyan:    #17a2b8 !default;

$colors: (
  blue: $blue,
  indigo: $indigo,
  purple: $purple,
  pink: $pink,
  red: $red,
  orange: $orange,
  yellow: $yellow,
  green: $green,
  teal: $teal,
  cyan: $cyan,
  white: $white,
  gray: $gray-600,
  gray-dark: $gray-800
) !default;

$theme-colors: (
  primary: #004278,
  primary-light: #0075be,
  primary-dark: #00325a,
  secondary: #ffd600,
  secondary-light: #ffef9a,
  success: $green,
  info: $cyan,
  warning: $yellow,
  danger: $red,
  light: #f8f7f2,
  dark: $gray-800
  );

  $headings-font-family: 'Palanquin Dark', sans-serif;
  $font-family-sans-serif: 'Palanquin', sans-serif;

  $animate-duration-sm: 0.2s;
  $shadow: 0 0.25rem 0.375rem rgba(#000, 0.125);

  $hamburger-layer-color: #fff;
  $hamburger-layer-width: 32px;
  $hamburger-layer-height: 2px;
  $hamburger-layer-spacing: 7px;
  $hamburger-layer-border-radius: 0;
